import { AccessControlList, SecurityGroupType } from '@youfoundation/transit-lib';
import Anonymous from '../Icons/Anonymous/Anonymous';
import ConnectedPeople from '../Icons/ConnectedPeople/ConnectedPeople';
import Globe from '../Icons/Globe/Globe';
import Shield from '../Icons/Shield/Shield';

const AclEditor = ({ acl }: { acl: AccessControlList }) => {
  const Icon = (props: { className: string }) => {
    return acl.requiredSecurityGroup === SecurityGroupType.Anonymous ? (
      <Globe {...props} />
    ) : acl.requiredSecurityGroup === SecurityGroupType.Authenticated ? (
      <Anonymous {...props} />
    ) : acl.requiredSecurityGroup === SecurityGroupType.Connected ? (
      <ConnectedPeople {...props} />
    ) : acl.requiredSecurityGroup === SecurityGroupType.Owner ? (
      <Shield {...props} />
    ) : (
      <Shield {...props} />
    );
  };

  return (
    <button
      title={acl.requiredSecurityGroup}
      className="mr-2 inline-block"
      onClick={(e) => e.preventDefault()}
    >
      <Icon className="h-5 w-5" />
    </button>
  );
};

export default AclEditor;
